.messaging-page-body {
	display: flex;
	justify-content: space-evenly;
}

.conversation-body {

}

.conversations-list {
	padding: 5px;
	display: flex;
	flex-direction: column;
}

.direct-user-container {
	padding: 5px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.direct-user-container:hover {
	cursor: pointer;
}

.user-profile-img {

}

.username {
	font-size: 12px;
	font-weight: 600;
}

.message-header {
	padding: 5px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.scrollbar
{
	margin-left: 30px;
	float: left;
	height: 75vh;
	width: 45vw;
	background: #F5F5F5;
	overflow-y: scroll;
	margin-bottom: 25px;
	/*transform: rotate(180deg);*/
	direction: ltr;
}

.force-overflow
{
	min-height: 2000px;
}

/*
 *  STYLE 6
 */

#style-6::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

#style-6::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

#style-6::-webkit-scrollbar-thumb
{
	background-color: #F90;	
	background-image: -webkit-linear-gradient(45deg,
	                                          rgba(255, 255, 255, .2) 25%,
											  transparent 25%,
											  transparent 50%,
											  rgba(255, 255, 255, .2) 50%,
											  rgba(255, 255, 255, .2) 75%,
											  transparent 75%,
											  transparent)
}


.message-wrapper-left {
	display: flex;
}

.message-wrapper-right {
	display: flex;
	justify-content: flex-end;
}

.message-body {
	display: flex;
	flex-direction: column;
}

.message-container {
	padding: 3px;
	font-size: 12px;
	font-weight: 450;
	margin: 5px;
	min-width: 90px;
	max-width: 60%;
	min-height: 8px;
	border-style: solid;
	border-width: 2px;
	border-color: black;
	border-radius: 9px;
	overflow-wrap: break-word;
}

.message-list {
	display: flex;
	flex-direction: column-reverse;
	justify-content: flex-start;
}

.message-footer {
	margin-left: 30px;
	display: flex;
	justify-content: space-between;
}

.message-input {
	bottom: 0px;
	width: 75%;
}